import { ThunkResult } from '../../types/thunk';
import {
  requestOrderStatus,
  requestOrderStatusError,
  requestOrderStatuses,
  requestOrderStatusesError,
  requestOrderStatusesSuccess,
  requestOrderStatusSuccess,
  uploadOrderStatus,
  uploadOrderStatusError,
  uploadOrderStatusSuccess
} from './actions';
import { addSuccessAlert, addToErrorHandler } from '../ErrorProvider/actions';
import { OrderStatusForm } from '../../types/OrderStatus';

export const fetchOrderStatuses = (): ThunkResult => async (dispatch, getState, { services }) => {
  try {
    dispatch(requestOrderStatuses());
    const { data } = await services.table.getEntities('orderStatus', {});
    dispatch(requestOrderStatusesSuccess(data.data));
  } catch (error) {
    dispatch(requestOrderStatusesError(error.response.status));
    dispatch(addToErrorHandler(error.response.status));
  }
};

export const fetchOrderStatus = (orderStatusId: string): ThunkResult => async (dispatch, getState, { services }) => {
  try {
    dispatch(requestOrderStatus());
    const { data } = await services.table.getEntities('orderStatus', {}, orderStatusId);
    dispatch(requestOrderStatusSuccess(data.data));
  } catch (error) {
    dispatch(requestOrderStatusError(error.response.status));
    dispatch(addToErrorHandler(error.response.status));
  }
};

export const createOrderStatus = (newOrderStatus: OrderStatusForm): ThunkResult => async (dispatch, getState, { services }) => {
  try {
    dispatch(uploadOrderStatus());
    const { data } = await services.table.postEntity('orderStatus', newOrderStatus);
    dispatch(uploadOrderStatusSuccess(data.data));
    dispatch(addSuccessAlert('Статус заказа создан'));
  } catch (error) {
    dispatch(uploadOrderStatusError(error.response.status));
    dispatch(addToErrorHandler(error.response.status));
  }
};

export const editOrderStatus = (newOrderStatus: OrderStatusForm, orderStatusId: string): ThunkResult => async (dispatch, getState, { services }) => {
  try {
    dispatch(uploadOrderStatus());
    const { data } = await services.table.putEntity('orderStatus', newOrderStatus, orderStatusId);
    dispatch(uploadOrderStatusSuccess(data.data));
    dispatch(addSuccessAlert('Статус заказа сохранен'));
  } catch (error) {
    dispatch(uploadOrderStatusError(error.response.status));
    dispatch(addToErrorHandler(error.response.status));
  }
};

export const deleteOrderStatus = (orderStatusId: string): ThunkResult => {
  return async (dispatch, getState, { services }) => {
    try {
      dispatch(uploadOrderStatus());
      const { data } = await services.table.deleteEntity('orderStatus', orderStatusId);
      dispatch(uploadOrderStatusSuccess(data.data));
      dispatch(addSuccessAlert('Статус заказа удален'));
    } catch (error) {
      dispatch(uploadOrderStatusError(error.response.status));
      dispatch(addToErrorHandler(error.response.status));
    }
  };
};
