import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { UseFormSetValue } from 'react-hook-form/dist/types/form';
import { Dispatcher } from '../../../../types/store';
import { OrderStatus, OrderStatusForm, OrderStatusState } from '../../../../types/OrderStatus';
import { fetchOrderStatus } from '../../../../store/OrderStatus/thunks';
import { useNavigate } from 'react-router-dom';
import { CarsState } from '../../../../types/Cars';
import { clearUploadedCarData } from '../../../../store/Cars/actions';
import { clearUploadedOrderStatusData } from '../../../../store/OrderStatus/actions';

export const useOrderStatusById = (orderStatusId: string, setValue: UseFormSetValue<OrderStatusForm>): [OrderStatus, boolean] => {
  const dispatch = useDispatch<Dispatcher>();
  const { orderStatusById, orderStatusByIdLoading, orderStatusByIdError } = useSelector<{
    orderStatus: OrderStatusState;
  }, OrderStatusState>((state) => state.orderStatus);

  const setValuesToForm = async (orderStatus): Promise<void> => {
    setValue('name', orderStatus.name, { shouldDirty: true });
    console.log(orderStatus);
  };

  useEffect(() => {
    if (orderStatusId) {
      dispatch(fetchOrderStatus(orderStatusId));
    }
  }, [orderStatusId]);

  useEffect(() => {
    if (orderStatusById && orderStatusId) {
      setValuesToForm(orderStatusById);
    }
  }, [orderStatusById]);

  return [orderStatusById, orderStatusByIdLoading];
};

export const useUploadedOrderStatus = (orderStatusId: string | null, navigate): boolean => {
  const dispatch = useDispatch<Dispatcher>();
  const { uploadedOrderStatus, uploadedOrderStatusLoading } = useSelector<{
    orderStatus: OrderStatusState;
  }, OrderStatusState>((state) => state.orderStatus);
  useEffect(() => {
    if (uploadedOrderStatus && !orderStatusId) {
      navigate(`/edit/car/${uploadedOrderStatus.id}`);
      dispatch(clearUploadedOrderStatusData());
    }
  }, [uploadedOrderStatus]);

  return uploadedOrderStatusLoading;
};
